<script>
  import axios from 'axios'; 
  axios.defaults.headers.common['Accept'] = 'application/json';
  axios.defaults.headers.common['X-Requested-With'] = 'XMLHttpRequest';
  axios.defaults.headers.common['Content-Type'] = 'application/json';

  export let year = new Date().getFullYear();
  export let award = '';
  export let items = [];
  export let size = 'original';
  export let caption = '';
  export let imgSrc = '';
  export let imgAlt = '';

  const reqYear = year || new Date().getFullYear() - 1;
  let index = -1;

  const url = `${"https://api-auszeichnungen.fmh.de"}/photos/${award}/awards/${reqYear}.json`;

  function populateImage(index) {
    const item = items[index];
    imgSrc = 
      `https://auszeichnungen.fmh.de/photos/images/${item.id}/${size}/${item.image_file_name}`;
    imgAlt = item.description;
    caption = item.title;
  }

  function prev() {
    if (!items || !items.length) return false;
    if (index === 0) return false; 
    index -= 1;
    populateImage(index); 

  }

  function next() {
    if (!items || !items.length) return false;
    if (index === items.length) return false; 
    index += 1;
    populateImage(index); 
  }

  axios.get(url)
  .then((res) => {
    if (!res.data || !res.data.length) return false;
    for (let i = 0, l = res.data.length; i < l; i++) {
      if (res.data[i].active) { 
        items = [...items, res.data[i]]; 
      }
    }
    index = 0;
    populateImage(index); 
  }).catch((err) => {
    console.error(err);
  });
</script>


{#if (items && items.length) }
  <div class="gallery">
    <figure class="gallery__figure">
      <img
        class="gallery__img"
        src="{imgSrc}" 
        alt="{imgAlt}" /> 
      <div class="gallery__nav">
        <a href="#blank" role="button"
          on:click|preventDefault={prev} class="gallery__prev"><span/></a>
        <a href="#blank" role="button"
          on:click|preventDefault={next} class="gallery__next"><span/></a>
      </div>
      <figcaption class="gallery__figcaption">
        {caption}
      </figcaption>
    </figure>
  </div>
{/if}


